import React, { useState, useEffect, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import api from '../services/api';
import PostCard from '../components/PostCard';
import LoadingSpinner from '../components/LoadingSpinner';
import { User, Mail, Calendar } from 'lucide-react';
import dayjs from 'dayjs';

const UserProfile = () => {
  const { userId } = useParams();
  const [user, setUser] = useState(null);
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

            useEffect(() => {
            fetchUserProfile();
          }, [fetchUserProfile]);

            const fetchUserProfile = useCallback(async () => {
            try {
              setLoading(true);
              const response = await api.get(`/api/users/${userId}`);
              const { user: userData, posts: userPosts } = response.data.data;
              setUser(userData);
              setPosts(userPosts);
            } catch (error) {
              console.error('Error fetching user profile:', error);
              setError('Failed to load user profile');
            } finally {
              setLoading(false);
            }
          }, [userId]);

  const handleEditPost = async (postId, content) => {
    try {
      const response = await api.put(`/api/posts/${postId}`, { content });
      const updatedPost = response.data.data.post;
      setPosts(prev => 
        prev.map(post => 
          post._id === postId ? updatedPost : post
        )
      );
    } catch (error) {
      console.error('Error editing post:', error);
      throw error;
    }
  };

  const handleDeletePost = async (postId) => {
    try {
      await api.delete(`/api/posts/${postId}`);
      setPosts(prev => prev.filter(post => post._id !== postId));
    } catch (error) {
      console.error('Error deleting post:', error);
      throw error;
    }
  };

  if (loading) {
    return <LoadingSpinner />;
  }

  if (error) {
    return (
      <div className="max-w-2xl mx-auto text-center py-12">
        <h2 className="text-xl font-medium text-gray-900 mb-2">Error</h2>
        <p className="text-gray-600">{error}</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="max-w-2xl mx-auto text-center py-12">
        <h2 className="text-xl font-medium text-gray-900 mb-2">User not found</h2>
        <p className="text-gray-600">The user you're looking for doesn't exist.</p>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto">
      {/* User Info Card */}
      <div className="card p-6 mb-6">
        <div className="flex items-start space-x-6">
          {/* Profile Picture */}
          <div className="flex-shrink-0">
            {user.profilePicture ? (
              <img 
                src={user.profilePicture} 
                alt={user.name}
                className="w-20 h-20 rounded-full object-cover"
              />
            ) : (
              <div className="w-20 h-20 bg-primary-600 rounded-full flex items-center justify-center">
                <span className="text-white font-bold text-2xl">
                  {user.name.charAt(0).toUpperCase()}
                </span>
              </div>
            )}
          </div>

          {/* User Details */}
          <div className="flex-1">
            <h1 className="text-2xl font-bold text-gray-900 mb-2">
              {user.name}
            </h1>
            
            <div className="space-y-2 text-sm text-gray-600">
              <div className="flex items-center space-x-2">
                <Mail size={16} />
                <span>{user.email}</span>
              </div>
              
              {user.bio && (
                <div className="flex items-start space-x-2">
                  <User size={16} className="mt-0.5" />
                  <span>{user.bio}</span>
                </div>
              )}
              
              <div className="flex items-center space-x-2">
                <Calendar size={16} />
                <span>Joined {dayjs(user.createdAt).format('MMMM YYYY')}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Posts Section */}
      <div className="mb-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">
          Posts by {user.name}
        </h2>
        
        {posts.length > 0 ? (
          <div className="space-y-4">
            {posts.map(post => (
              <PostCard
                key={post._id}
                post={post}
                onEdit={handleEditPost}
                onDelete={handleDeletePost}
              />
            ))}
          </div>
        ) : (
          <div className="text-center py-8">
            <p className="text-gray-600">
              {user.name} hasn't posted anything yet.
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default UserProfile; 